.CharacterList-section {
  margin: auto;
}

.CharacterList {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  margin: 60px;
  @media all and (max-width:768px){
    margin: 5px;
  }
  &__notfound {
    background-color: rgb(14, 145, 145);
    border: 3px solid #41f210;
    border-radius: 10px;
    height: 150px;
    width: 500px;
    color: white;
    margin: auto;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    margin-top: 25px;
    margin-bottom: 50px;
    @media all and (max-width:768px){
      width: 220px;
    }
    &--word {
      font-weight: bold;
      margin-top: 10px;
      font-size: 20px;
    }
  }
}